background-color: #f0f0f3;\r\n    color: var(--muted-text-color);\r\n    border: 1px solid var(--border-color);\r\n    border-radius: 6px;\r\n    padding: 0.5rem 1rem;\r\n    font-weight: 500;\r\n    cursor: pointer;\r\n    transition: background-color 0.2s ease, color 0.2s ease;\r\n\r\n    &:hover {\r\n        background-color: #e0e0e5;\r\n        color: var(--text-color);\r\n    }\r\n\r\n    &:disabled {\r\n        background-color: #f5f5fa;\r\n        color: var(--muted-text-color);\r\n        cursor: not-allowed;\r\n        opacity: 0.7;\r\n    }\r\n}\r\n\r\n@import \"./header\";\r\n@import \"./employee\";\r\n@import \"./search\";\r\n@import \"./profile\";\r\n@import \"./user-details\";\r\n@import \"./auth\";",".header {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    background-color: var(--primary-color);\r\n    color: #fff;\r\n    padding: 12px 24px;\r\n    width: 100%;\r\n    height: 60px;\r\n    box-sizing: border-box;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    z-index: 1001;\r\n\r\n    &__left {\r\n        display: flex;\r\n        flex-direction: column;\r\n        gap: 2px;\r\n\r\n        h3,\r\n        h1 {\r\n            margin: 0;\r\n            color: #fff;\r\n        }\r\n\r\n        h3 {\r\n            font-size: 12px;\r\n            font-weight: 500;\r\n            letter-spacing: 1px;\r\n        }\r\n\r\n        h1 {\r\n            font-size: 16px;\r\n            font-weight: 700;\r\n            letter-spacing: 1.2px;\r\n        }\r\n\r\n        &.company-link {\r\n            text-decoration: none;\r\n            color: inherit;\r\n            transition: color 0.2s ease, text-shadow 0.2s ease;\r\n\r\n            &:hover {\r\n                text-shadow: 0 2px 6px rgba(0, 0, 0, 0.8);\r\n                color: #faebb3;\r\n            }\r\n        }\r\n    }\r\n\r\n    &__center {\r\n        flex: 1;\r\n        text-align: center;\r\n\r\n        .address-book-title